	import service from '../../service.js';
import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
	export default {
		components: {
			uniNumberBox
		
		},
		data() {
			return {
				// rmbprice:'',
				type:'',
				verifycode:'',
				countdown: 62,
				numChange:'',
				numberValue: 0.00,  // 获取输入的值
				data:uni.getStorageSync("data_fo"),
				num: "",
				password: '',
				captcha:'',//图形验证
				uniqid:'',
				allnum:'',
				now_price:'',
				ss:0,
				// allNums:'',
				verification: [{
						name: 'num',
						title: '请输入数量'
					},
					{
						name: 'psw',
						title: '请输入置换密码'
					},
					{
						name: 'code',
						title: '请输入验证码'
					}
				],

			}
		},
		// computed:{
		// 	comput(){
		// 		this.allnum = this.number*this.now_price
		// 		return this.allnum
		// 	}
		// },
		
		
		computed:{
			allNums(){
				var str = uni.getStorageSync("tr")
				 var AllPrice = this.num * this.numberValue * str       
				return AllPrice.toFixed(2); //保留两位小数的字符串
			},
			dollars(){
				var AllPrice = this.num * this.numberValue 
				return AllPrice.toFixed(2); //保留两位小数的字符串
			},
			rmbprice(){
				var AllPrice =Number(this.numberValue *  uni.getStorageSync("tr")) 
				return AllPrice.toFixed(2);
			}
		},
		methods: {
			//手機驗證碼獲取倒計時
			codetime: function(obj, num) {
				if (num < 10) {
					obj.countdown = "0" + num;
				} else {
					obj.countdown = num;
				}
				num--;
				if (num < 0) {
					obj.countdown = 61;
				} else {
					setTimeout(function() {
						obj.codetime(obj, num);
					}, 1000);
				}
			},
			send() {
				var self = this
				uni.request({
					url: service.api.index.verifycode, 
					method: 'GET',
					data: {
						'temp': 'sms_buy',
					},
					
					header: {
						Authorization: 'Bearer ' + uni.getStorageSync('token')
					},
					success: res => {
						console.log(res)
						var res = res.data;
						if (res.code == 1) {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							self.codetime(self, 60);
						} else {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							return;
						}
					}
				});
				// this.codetime(this,60);
			},
			emailValue(){
				var self = this
				uni.request({
					url: service.api.reg.sendEmailCode, 
					method: 'GET',
					data: {
						'email': uni.getStorageSync("member").email,
					},
					success: res => {
						console.log(res)
						var res = res.data;
						if (res.code == 1) {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							self.codetime(self, 60);
						} else {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							return;
						}
					}
				});
			},
			change(value) {
				this.numberValue = Number(value).toFixed(2) 
				this.numChange = value
				console.log(value)
			},
			jump(e){
				uni.switchTab({
					url:e
				});
			},
			inputss(){
				
			},
			submit() {
				let canNext = this.verification.some(item => {
					
					if(this.numberValue > this.data.mining_now_max_price){
						uni.showToast({
							icon: 'none',
							title: '超过限制无效！'
						})
						return
					}
					
					if (this[item.name] == '') {
						uni.showToast({
							icon: 'none',
							title: item.title
						})
					}
					return this[item.name] == ''
				})
				if (!canNext) {
					//  这里发请求
					// this.captchas();
					/* this.$service.auth(this,this.$service.api.center.submitPurchasing,"POST",{
						num:this.num,
						password:this.password,
						verifycode:this.verifycode,
						uniqid:this.uniqid
					},(self,res)=>{
						// console.log(res)
						if(res.code == 1){
							setTimeout(()=>{
								uni.navigateTo({
									url:'/pages/center/my_deal' 
								})
							},1500)
						}
						uni.showToast({
							icon:'none',
							title:res.info
						});
					}); */
					
					uni.request({
						url:this.$service.api.center.submitPurchasing,
						method:"POST",
						data:{
							num:this.num,
							password:this.password,
							verifycode:this.verifycode,
							uniqid:this.uniqid,
							type:this.type,
							purchase_price:this.numberValue * uni.getStorageSync("tr"),
						},
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						success: (res) => {
							if(res.data.code == 1){
								uni.showToast({
								icon:'none',
								title:res.data.info
							});
								
								setTimeout(()=>{
									uni.navigateTo({
										url:'/pages/center/my_deal' 
									})
								},1500);
								
							}else{
								uni.showToast({
									icon:'none',
									title:res.data.info,
									duration:2000
								});
								this.captchas();
							}
							
						},
						fail: (res) => {
							uni.showToast({
								icon:'none',
								title:res.data.info
							});
						}
					});
				}
			},
			captchas(){//图形验证
				/* this.$service.auth(this,this.$service.api.home.captcha,"GET",{
					// s:'think/admin/captcha'
				},(self,res)=>{
					console.log(res)
					self.captcha = res.data.image;
					self.uniqid = res.data.uniqid;
				}); */
				
				uni.request({
					url:this.$service.api.home.captcha,
					method:"GET",
					header: {
						Authorization: 'Bearer ' + uni.getStorageSync('token')
					},
					success: (res) => {
						var res = res.data;
						console.log(res , "captcha");
						this.captcha = res.data.image;
						this.uniqid = res.data.uniqid;
					}
				});
				
			}
		},
		onShow(){
			this.captchas()	
			this.numberValue = this.data.mining_now_min_price
		var ttt = uni.getStorageSync('type');
			// this.rmbprice = Number(this.numberValue * uni.getStorageSync("tr")).toFixed(2) 
		this.type = ttt
		},
		onLoad(op) {
			var sss =parseFloat(op.now_price) 
			this.now_price = sss;
			/* var a = uni.getStorageSync("data_fo")
			this.now_price = (Number(a.mining_now_min_price)+Number(a.mining_now_max_price))/2 */
				var str = uni.getStorageSync("tr")
				console.log(str,66666)
		}
	}
